.Home_container {
    position: absolute;
    z-index: -1;
    width: 100vw;
    height: 100%;
    overflow-x: hidden;
}

.Home_bg {
    position: absolute;
    top: 5.7vmin;
    left: 4.9vmin;
    right: 4.9vmin;
    margin: 0 auto;
    /* overflow-y: hidden; */
    height: 67vmin;
    background: #0C0B38;
    border-radius: 5vmin;
    border: 0.3vmin solid;

    border: 1px solid;
    border-top-color: #EDEDED;
    border-right-color: #EDEDED;
    border-left-color: #E0E0E0;
    border-bottom-color: #E0E0E0;
}

.home_top_text {
    margin-top: 8%;
    text-align: center;
    font-family: var(--font-family-Comfortaa);
    font-size: 8.5vmin;
    text-transform: capitalize;
    color: #EBEFFF;
}
.home_text {
    position: absolute;
    width: 55vw;
    top: 30vmin;
    left: 0;
    right: 0;
    margin: 0 auto;;
    font-family: var(--font-family-Urbanist);
    font-style: normal;
    font-size: 3vmin;
    line-height: 5vmin;
    text-align: center;
    letter-spacing: 0.06vmin;
    color: #CBD4FF;
}

.Home_register {
    position: absolute;
    top: 70%;
    left: 35%;
    background: linear-gradient(89.95deg, rgba(50, 177, 38, 0.7) 1.99%, rgba(36, 162, 233, 0.7) 85.34%, rgba(36, 162, 233, 0.7) 104.58%);
    outline: 0;
    border-radius: 2.3vmin;
    border: none;
    font-family: var(--font-family-Comfortaa);
    font-size: 3vmin;
    font-weight: 600;
    cursor: pointer;
    color: #FFFFFF;
    height: 7.8vmin;
    width: 24vmin
}

